<template>
  <div>
    <h1>({{ categories.length }}) Categorieën</h1>
    <button @click="newCategory" class="button__text">
      Voeg categorie toe
    </button>
    <tc-categories :categories="categories" :masterData="masterData" />
  </div>
</template>

<script>
// @ is an alias to /src
import Categories from "@/components/categories/Categories.vue";
import NewCategoryModal from "@/components/modals/NewCategoryModal.vue";

export default {
  beforeCreate: function() {
    this.$store.dispatch("setCategories");
    this.$store.dispatch("setMasterData");
  },
  data: function() {
    return {};
  },
  computed: {
    masterData() {
      return this.$store.getters.getMasterData;
    },
    categories() {
      return this.$store.getters.getCategories;
    }
  },
  name: "CategoriesPage",
  components: {
    "tc-categories": Categories
  },
  methods: {
    newCategory: function() {
      this.$modal.show(
        NewCategoryModal,
        {
          title: "Nieuwe categorie",
          id: -1,
          name: ""
        },
        {
          scrollable: true,
          height: "auto"
        }
      );
    }
  }
};
</script>
