<template>
  <div>
    <h1>({{ customers.length }}) Bedrijven</h1>
    <button @click="newCustomer" class="button__text">Voeg bedrijf toe</button>
    <tc-customers :customers="customers" :masterData="masterData" />
  </div>
</template>

<script>
// @ is an alias to /src
import Customers from "@/components/customers/Customers.vue";
import NewCustomerModal from "@/components/modals/NewCustomerModal.vue";

export default {
  beforeCreate: function() {
    this.$store.dispatch("setCustomers");
    this.$store.dispatch("setMasterData");
  },
  data: function() {
    return {};
  },
  computed: {
    masterData() {
      return this.$store.getters.getMasterData;
    },
    customers() {
      return this.$store.getters.getCustomers;
    }
  },
  name: "CustomersPage",
  components: {
    "tc-customers": Customers
  },
  methods: {
    newCustomer: function() {
      this.$modal.show(
        NewCustomerModal,
        {
          title: "Nieuwe bedrijf",
          id: -1,
          name: "",
          image: ""
        },
        {
          scrollable: true,
          height: "auto"
        }
      );
    }
  }
};
</script>
