<template>
  <div>
    <h1>({{ contacts.length }}) Contactpersonen</h1>
    <button @click="newContact" class="button__text">
      Voeg contactpersoon toe
    </button>
    <tc-contacts :contacts="contacts" :masterData="masterData" />
  </div>
</template>

<script>
// @ is an alias to /src
import Contacts from "@/components/contacts/Contacts.vue";
import NewContactModal from "@/components/modals/NewContactModal.vue";

export default {
  beforeCreate: function() {
    this.$store.dispatch("setContacts");
    this.$store.dispatch("setMasterData");
  },
  data: function() {
    return {};
  },
  computed: {
    masterData() {
      return this.$store.getters.getMasterData;
    },
    contacts() {
      return this.$store.getters.getContacts;
    }
  },
  name: "ContactsPage",
  components: {
    "tc-contacts": Contacts
  },
  methods: {
    newContact: function() {
      this.$modal.show(
        NewContactModal,
        {
          title: "Nieuw contactpersoon",
          id: -1,
          name: "",
          mail: "",
          slackName: ""
        },
        {
          scrollable: true,
          height: "auto"
        }
      );
    }
  }
};
</script>
